import { BrowserRouter as Router } from 'react-router-dom';
import { NavHashLink, HashLink } from 'react-router-hash-link';
import { useState } from 'react';
// @ts-ignore
import resume from '../../assets/Srikar_Hasthi_Resume.pdf';
import "./Header.scss";

// import CV from '../../assets/CV_VinayakSingh.pdf' 
export const Header = () => {
  const [isActive, setActive] = useState(false)

  // function toggleTheme() {
  //   let html = document.getElementsByTagName('html')[0]
  //   html.classList.toggle('light')
  // }

  function closeMenu() {
    setActive(false)
  }

  return (
    <div className="header-fixed">
      <Router>
        <HashLink smooth to="#home" className="logo">
          <span>Srikar</span>
          <span> Hasthi</span>
        </HashLink>

        {/* <input
          onChange={toggleTheme}
          className="container_toggle"
          type="checkbox"
          id="switch"
          name="mode"
        />
        <label htmlFor="switch">Toggle</label> */}

        <nav className={isActive ? 'active' : ''}>
          <NavHashLink smooth to="#home" onClick={closeMenu}>
            Home
          </NavHashLink>
          <NavHashLink smooth to="#aboutMe" onClick={closeMenu}>
            About me
          </NavHashLink>
          <NavHashLink smooth to="#project" onClick={closeMenu}>
            Project
          </NavHashLink>
          <NavHashLink smooth to="#contact" onClick={closeMenu}>
            Contact
          </NavHashLink>
          <a href={resume} download="Srikar_Hasthi_Resume.pdf" className="button">
            Resume 
          </a>
        </nav>

        <div
          // aria-expanded={isActive ? 'true' : 'false'}
          // aria-haspopup="true"
          // aria-label={isActive ? 'Fechar menu' : 'Abrir menu'}
          className={isActive ? 'menu active' : 'menu'}
          onClick={() => {
            setActive(!isActive)
          }}
        ></div>
      </Router>
    </div>
  )
}
